// @require '../../style/reset.scss'

import React from 'react';
import ReactDom from 'react-dom';
import Datepicker from '../index';

class Demo extends React.Component {
  constructor(props) {
    super(props);

    this.handleSelect = this.handleSelect.bind(this);

    this.state = {
      logArr: []
    };
  }

  handleSelect(newDate, oldDate) {
    const logs = [`newDate: ${newDate}`, `oldDate: ${oldDate}`];

    this.setState({
      logArr: logs
    });
  }

  renderLogs() {
    return this.state.logArr.map((log, index) => {
      return (
        <p
          key={index}
        >
          {log}
        </p>
      );
    });
  }

  render() {
    return (
      <div>
        <h2>CallBack On Change Selected Date</h2>
        <div>{this.renderLogs()}</div>
        <Datepicker
          placeholder={'点击选择日期'}
          onChange={this.handleSelect}
        />
      </div>
    );
  }
}

ReactDom.render(
  <Demo />,
  document.getElementById('demo9')
);
